* {
  box-sizing: border-box;
  margin: 0;
}

header {
  padding: 30px 0 30px 0;
  background: #343a40;
  color: #fff;
  text-align: center;
  font-family: 'Alatsi', sans-serif;

}

header h1 {
  display: inline;
  margin-left: 15px;
  font-weight: 300;
}

.search-area {
  margin: 15px;
  display: flex;
  justify-content: center;
  width: 100%;
}

.search-area input {
  height: 30px;
  width: 250px;
}

.search-area button, select{
  margin-left: 10px;
  background-color: #343A40;
  color: #fff;
  font-family: 'Alatsi', sans-serif;
  font-size: medium;
  height: 30px;
  width: 70px;
  border-radius: 10%;

}

.list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}

.card-container {
  width:800px;
  box-shadow: 0 0 1px 1px #888;
  margin: 5px auto;
  border-radius: 5px;
	overflow: hidden;
  display: flex;
  flex-direction: row;
}

.card-container img {
  display: block;
  margin: auto 20px auto 10px;
  padding-top: 20px;
  padding-bottom: 20px;
  width: 30%;
  height: 40%;
}

.desc {
  height: auto;
  padding: 10px;
  align-content: left;
}

.desc h2 {
  font-family: 'Alatsi', sans-serif;
  font-size: 1.5em;
  font-weight: 500;
}
.desc h3, h4 {
  color: #6c757d;
  font-size: .9em;
  font-weight: 300;
  margin: 0;
}

.desc p {
  padding: 5px 0 0 0;
  top: 25px;
  font-size: 1em;
  font-weight: 500;
  color: #343A40;
  font-family: 'Roboto', sans-serif;
}

.view {
  display: flex;
  justify-content: left;
  margin: 10px;
  bottom: 30px;
  font-family: 'Alatsi', sans-serif;
}

.btn btn-primary{
  color: #fff;
  border-color: #343a40;
}

footer {
  position: relative;
  margin-top: 50px;
  bottom: 0;
  width: 100%;
  height: 100px; 
  background: #343a40;
  color: #fff;
  text-align: center;
  font-family: 'Alatsi', sans-serif;
}

.comment {
  padding: 30px 0 30px 0;
}